import React, { useEffect, useState } from "react";
import { Button, Result } from "antd";
import Head from "next/head";
import { useRouter } from "next/router";
import { useSelector } from "react-redux";
const CError = ({ status, handleBack }) => {
  const { title } = useSelector((state) => state.title);
  const router = useRouter();
  const [pageTitle, setPageTitle] = useState("Not Found");
  const [subTitle, setSubTitle] = useState(
    "Sorry, the page you visited does not exist."
  );
  useEffect(() => {
    if (status === 403) {
      setPageTitle("Forbidden");
      setSubTitle("Sorry, you are not authorized to access this page.");
    } else if (status === 500) {
      setPageTitle("Server Error");
      setSubTitle("Sorry, something went wrong.");
    }
  }, []);
  return (
    <>
      <Head>
        <title>{pageTitle === "" ? title : pageTitle + " | " + title}</title>
        <meta
          property="og:title"
          content={pageTitle === "" ? title : pageTitle + " | " + title}
        />
      </Head>
      <Result
        status={status}
        title={pageTitle}
        subTitle={subTitle}
        extra={
          <Button
            onClick={handleBack ? handleBack : () => router.back()}
            type="primary"
          >
            {status === 500 ? "Reload" : "Back"}
          </Button>
        }
      />
    </>
  );
};
export default CError;
